.header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 3;
}
.header__wrap {
  display: flex;
  align-items: flex-start;
  padding: 24px 0;
}
.header .logo {
  max-width: 120px;
}
.header .logo.opacity {
  opacity: 0.5;
}
.header__menu {
  display: flex;
  list-style: none;
  margin: 14px 114px 0 auto;
  padding: 0;
  font-family: 'Manrope', sans-serif;
  font-weight: 600;
  font-size: 16px;
}
.header__menu-item:not(:last-of-type) {
  margin-right: 74px;
}
.header__menu a {
  position: relative;
  z-index: 2;
  transition: 0.3s;
}
.header__menu a:before {
  content: '';
  position: absolute;
  top: -3px;
  left: -12px;
  width: 100%;
  height: 100%;
  background-color: #9a61a5;
  z-index: -1;
  border-top-left-radius: 0;
  border-bottom-right-radius: 0;
  width: calc(100% + 25px);
  height: calc(100% + 7px);
  pointer-events: none;
  opacity: 0;
  transition: 0.3s;
}
.header__menu a:hover {
  color: #ffffff;
}
.header__menu a:hover:before {
  opacity: 1;
  border-top-left-radius: 12px;
  border-bottom-right-radius: 12px;
}
.header__phone {
  font-size: 16px;
  font-weight: 600;
  font-family: 'Manrope', sans-serif;
  margin-top: 18px;
}
.header .burger {
  display: none;
  position: relative;
  width: 24px;
  height: 18px;
  margin-top: 15px;
  margin-left: 15px;
}
.header .burger span {
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  transition: 0.3s;
  transform: rotate(0);
  opacity: 1;
}
.header .burger span:nth-of-type(1) {
  bottom: auto;
}
.header .burger span:nth-of-type(3) {
  top: auto;
}
.header .burger.active span:nth-of-type(1) {
  transform: rotate(45deg);
  bottom: 0;
}
.header .burger.active span:nth-of-type(2) {
  opacity: 0;
}
.header .burger.active span:nth-of-type(3) {
  transform: rotate(-45deg);
  top: 0;
}
